
import { BlockRenderer } from "@/components/custom/block-renderer";
import { BlocksContent, TLink } from "@/types";
import { AwardIcon, UsersIcon, VideoIcon } from "lucide-react";
import Link from "next/link";
import { Button } from "@/components/ui/button";

export interface CtaFeature {
  id: number;
  __component: string;
  heading: string;
  subHeading: string;
  features: BlocksContent; // ‚Üê This is the rich text blocks field
  tier: string;
  location: string;
  icon: string;
  mostPopular: boolean;
  link: TLink;
}

export interface CtaFeaturesSectionProps {
  id: number;
  documentId: string;
  __component: string;
  heading: string;
  subHeading: string;
  description: string;
  features: CtaFeature[];
}

function getIcon(name: string) {
  switch (name) {
    case "VIDEO_ICON":
      return <VideoIcon className="w-4 h-4 text-gray-900" />;
    case "USERS_ICON":
      return <UsersIcon className="w-4 h-4 text-gray-900" />;
    case "AWARD_ICON":
      return <AwardIcon className="w-4 h-4 text-gray-900" />;
    default:
      return null;
  }
}

export async function CtaFeaturesSection({data}: { data: CtaFeaturesSectionProps }) {
  if (!data?.features) return null;
  return (
    <section className= "px-8 py-4 mx-auto md:px-6 lg:py-12 bg-brand-cream">
      <div className="container mx-auto px-4">
        {/* Section Header */}
        <div className="mb-12 text-center">
          <h2 className="mb-2 text-xl text-brand-gold font-extrabold">{data.heading}</h2>
          <p className="text-xl text-black">{data.subHeading}</p>
          {data.description && (
            <p className="mx-auto mt-4 max-w-2xl text-brand-dark-grey">{data.description}</p>
          )}
        </div>

        {/* Features Grid */}
        <div className="grid gap-8 md:grid-cols-2 lg:grid-cols-3 max-w-6xl mx-auto">
          {data.features.map((item: CtaFeature) => (
            <div
              key={item.id}
              className={`rounded-xl border bg-white my-4 p-6 shadow-sm transition-shadow hover:shadow-md relative flex flex-col ${
                item.mostPopular ? "border-brand-gold border-2" : ""
              }`}
            >
              {/* Most Popular Badge */}
              {item.mostPopular && (
                <div className="absolute top-0 right-0 bg-brand-gold text-black text-xs px-3 py-1 rounded-bl-md rounded-tr-md">
                  Most Popular
                </div>
              )}
              
              {/* Feature Header */}
              <div className="my-4">
                <div className="flex items-start mb-4">
                  <div className="rounded-full bg-brand-cream p-2 inline-flex items-center justify-center">
                    {getIcon(item.icon)}
                  </div>
                </div>
                <h3 className="text-lg">{item.heading}</h3>
                <p className="text-sm text-brand-gold font-extrabold">{item.subHeading}</p>
                <div className="text-sm font-medium py-1 mb-2 text-brand-dark-grey">
                  {item.location}
                </div>
              </div>
              <div className="text-sm text-brand-gold font-extrabold">{item.tier}</div>
              {/* Rich Text Content - This is the blocks field */}
              <div className="flex my-4">
                <BlockRenderer content={item.features} />
              </div>
              
              {/* CTA Link */}
              {item.link && (
                <Link
                  href={item.link.href}
                  target={item.link.isExternal ? "_blank" : undefined}
                  rel={
                    item.link.isExternal ? "noopener noreferrer" : undefined
                  }
                  className="mt-auto mb-2"
                >
                  <Button className={`w-full text-black border border-brand-grey ${item.mostPopular ? "bg-brand-gold hover:bg-brand-gold/90" : "bg-brand-cream hover:bg-brand-cream/90"}`}>
                    {item.link.label}
                  </Button>
                </Link>
              )}
            </div>
          ))}
        </div>
      </div>
    </section>
  );
}
